import React, { useState, useEffect } from "react"
import { Link } from "gatsby"
import fetch from "node-fetch"
import Layout from "../components/layout"
import SEO from "../components/seo"
import Beer from "../components/beer"
import { createGlobalStyle, keyframes } from "styled-components"

const slide = keyframes`
 0%   { transform: translateX(200%); }
 100% { transform: translateX(0%); }
`

const GlobalStyle = createGlobalStyle`
  html {
    height: 100%;
  }
  body {
    background: linear-gradient(to bottom, #3494e6, #ec6ead);
    height: 100%;
    margin: 0;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: contain;
    background-position: center;
    }
    body::after {
      content: '';
      display: block;
      z-index: -1;
      background: url(${props => props.bodyImage});
      background-repeat: no-repeat;
      background-attachment: fixed;
      background-size: contain;
      animation: ${slide} 5s cubic-bezier(.17,.67,.83,.67);
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
  }
`

const Beers = () => {
  const [beers, setBeers] = useState(false);
  const [backgroundImg, setBackgroundImg] = useState("");
  useEffect(() => {
    let url = `https://api.punkapi.com/v2/beers/`;
    fetch(url).then(resp => resp.json()).then(data => setBeers(data));
  }, [])
  return (
    <Layout>
      <GlobalStyle bodyImage={backgroundImg}/>
      <SEO title="Page three" />
      {beers && beers.map(beer => {
        return (
          <Beer onMouseOver={() => setBackgroundImg(beer.image_url)} onMouseOut={() => setBackgroundImg("")} beerInfo={beer}/>
        )
      })}
      <Link to="/">Go back to the homepage</Link>
    </Layout>
  )
}

export default Beers
