import React from "react"
import styled from "styled-components"
import { Link } from "gatsby"

const ImgStyled = styled.img`
  height: 100px;
`

const Beer = ({ beerInfo, onMouseOver, onMouseOut }) => {
  return (
    <div onMouseOut={onMouseOut} onMouseOver={onMouseOver}>
      <h1>Hi, im {beerInfo.title}</h1>
      <ImgStyled src={beerInfo.image_url}/>
      <Link to={`/beer/${beerInfo.id}`}>More details</Link>
    </div>
  )
}

export default Beer
